@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.landing{
  background: url(/mockCansat/static/media/rocket.a47ab895.gif);
  background-size: cover;
  margin-bottom: 0px !important;
}
.team{
  background: url(/mockCansat/static/media/banner.e5f4d961.jpg);
  background-size: cover;
  padding: 10em !important;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


hr.dashed {
  /* border-top: 2px dashed #999; */
}
.subcardimg img {height: 35vh;}
#multi-item-example .carousel-indicators li {background-color: #666;}
.homeslider img{
	height: 100vh;
}

.electrical{
  background: url(/mockCansat/static/media/ebanner.aca5fe2c.jpg);
  background-size: cover;
  padding: 10em !important;
}
.carrier{
  background: url(/mockCansat/static/media/carbanner.ba7ec9f0.jpg);
  background-size: cover;
  padding: 10em !important;

}
.payload{
  background: url(/mockCansat/static/media/banner_2.4fa213e8.PNG);
  background-size: cover;
  padding: 11em !important;
}


.para {text-align: justify;}
.mission {
  background: url(/mockCansat/static/media/banner.41eea27d.jpg);
  background-size: cover;
  padding: 30vh !important;
}
.videocs {
  height: 55%;
  width: 100%;
}
 .justify-content {text-align: justify;} 
.scienceimg {
  height: 60vh !important;
  width: 100%;
}
.heatimg{width:50%;float: left; margin-right: 5%;}
p{font-size: 16px !important;}
