/*** 

====================================================================
  Loading Transition
====================================================================

 ***/
#loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background: #fff;
  overflow: hidden;
}
#preloader_1{
  position: absolute;
  left: 50%;
  margin-left: -25px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
#preloader_1 span{
  display:block;
  bottom:0px;
  width: 6px;
  height: 5px;
  background:#e04f5d; 
  position:absolute;
  -webkit-animation: preloader_1 1.5s  infinite ease-in-out;
          animation: preloader_1 1.5s  infinite ease-in-out;
}
 
#preloader_1 span:nth-child(2){
left:11px;
-webkit-animation-delay: .2s;
        animation-delay: .2s;
 
}
#preloader_1 span:nth-child(3){
left:22px;
-webkit-animation-delay: .4s;
        animation-delay: .4s;
}
#preloader_1 span:nth-child(4){
left:33px;
-webkit-animation-delay: .6s;
        animation-delay: .6s;
}
#preloader_1 span:nth-child(5){
left:44px;
-webkit-animation-delay: .8s;
        animation-delay: .8s;
}
@-webkit-keyframes preloader_1 {
    0% {height:5px;-webkit-transform:translateY(0px);transform:translateY(0px);background:#e62d3f;}
    25% {height:30px;-webkit-transform:translateY(15px);transform:translateY(15px);background:#e62d3f;}
    50% {height:5px;-webkit-transform:translateY(0px);transform:translateY(0px);background:#ee2438;}
    100% {height:5px;-webkit-transform:translateY(0px);transform:translateY(0px);background:#ee2438;}
}
@keyframes preloader_1 {
    0% {height:5px;-webkit-transform:translateY(0px);transform:translateY(0px);background:#e62d3f;}
    25% {height:30px;-webkit-transform:translateY(15px);transform:translateY(15px);background:#e62d3f;}
    50% {height:5px;-webkit-transform:translateY(0px);transform:translateY(0px);background:#ee2438;}
    100% {height:5px;-webkit-transform:translateY(0px);transform:translateY(0px);background:#ee2438;}
}


